    header ul.mmenu>li {
        margin: 10px 8px 0
    }

    header ul.mmenu>li:after {
        content: "";
        display: block;
        width: 100%;
        height: 10px
    }

    header ul.mmenu>li:hover a.hasmenu {
        color: #F7AE21
    }

    header ul.mmenu>li:hover a.hasmenu i {
        border-color: #F7AE21
    }

    header ul.mmenu>li:hover .subBox {
        visibility: visible;
        opacity: 1;
        pointer-events: all
    }

    header ul.mmenu>li:first-child {
        z-index: 20
    }

    header ul.mmenu>li:nth-child(2) {
        z-index: 20
    }

    header ul.mmenu>li:nth-child(3) {
        z-index: 20
    }

    header ul.mmenu>li:nth-child(4) {
        z-index: 20
    }

    header ul.subBox {
        visibility: hidden;
        pointer-events: none;
        opacity: 0;
        -webkit-transition: 0s;
        transition: 0s;
        position: absolute;
        top: 2em;
        left: 50%;
        -webkit-transform: translateX(-50%);
        transform: translateX(-50%)
    }

    header ul.subBox a {
        width: 200px;
        text-align: center
    }

    header ul.subBox a:hover {
        color: #F7AE21
    }

    header .searchbx {
        min-width: 160px;
        -webkit-box-flex: 0;
        -ms-flex: 0 0 14%;
        flex: 0 0 14%;
        margin-right: 20px
    }

    header .mo_use {
        display: none
    }
}

